.remember-me-label {
	padding: 0 10px;
	margin: 6px 0 6px 0;
}

.checkbox_label {display: none;}

.checkbox_label + label {
	color: #656565;
    font: 400 13px/14px "Helvetica Neue", "Open Sans", "Segoe UI", sans-serif;
}

.checkbox_label + label:before {
	content: '';
	display: block;
	float: left;
	width: 14px;
	height: 14px;
	margin-top: 9px;
	margin-right: 10px;
	background: url('data:image/png;base64,') no-repeat;
	background-size: contain;
}

.checkbox_label:checked + label:before {background-image: url('data:image/png;base64,');}


