
* {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
body, html {
    background-color: #f5f5f6;
    height: 100%;
    width: 100%;
    font-family: 'Alternate Gothic LT No3', Quicksand, Open Sans, Segoe UI, sans-serif;
    font-size: 16px;
    font-weight: 600;
}

#root {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}

.app {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: column;
        flex-flow: column;
    -ms-flex-align: center;
        align-items: center;
    width: 100%;
    max-width: 500px;
    padding: 14px 10px;
}

.main_pic {
	display: block;
    /*width: 42px;*/
    /*height: 42px;*/
	margin: 0 auto 10px;
}

.app .StripeElement {
    margin: 6px 0;
}

input.name {
	height: 32px;
	line-height: 32px;
    outline: 0;
    padding: 0 10px;
    margin: 3px 0;
    width: 100%;
    font-size: 14px;
    font-family: "Helvetica Neue", "Open Sans", "Segoe UI", sans-serif;
    font-weight: 400;
    -webkit-text-fill-color: #424770;
    -webkit-font-smoothing: antialiased;
    border: 1px solid #cacaca;
	border-radius: 6px;
}

input.name:last-of-type {margin-bottom: 0;}

input.name::-webkit-input-placeholder{
    -webkit-text-fill-color: #b9b9b9;
}


input.name:focus::-webkit-input-placeholder {
    -webkit-text-fill-color: #b9b9b9;
}


div.expAndCvc{
    display: -ms-flexbox;
    display: flex;
}
div.expAndCvc .StripeElement {
    width: 50%;
}

select {
    margin: 8px 0;
    height: 40px;
    line-height: 40px;
    font-size: 16px;
    font-weight: 600;
    border: 0;
    outline: 0;
    color: #9BACC8;
    background-color: white;
    width: 100%;
}

form {
    width: 100%;
}

.Checkout {
    max-width: 800px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    padding: 0 5px;
}

button {
	height: 25px;
	width: 100%;
	margin-top: 10px;
	border: none;
	color: #fff;
	font: 400 13px "Helvetica Neue", "Open Sans", "Segoe UI", sans-serif;
	background-color: #0284ff;
	border-radius: 6px;
}

button:disabled {
    background-color: #dddddd;
}

.StripeElement, .remember-me-label {
    display: block;
    margin: 10px 0 20px 0;
    max-width: 500px;
    padding: 7px 10px;
    background: white;
    font-size: 14px;
    font-family: "Alternate Gothic LT No3", "Open Sans", "Arial", sans-serif;
    font-weight: 400;
    -webkit-font-smoothing: antialiased;
	height: 32px;
	line-height: 32px;
	border: 1px solid #cacaca;
	border-radius: 6px;
}

.w50 {
	overflow: hidden;
}

.w50 .StripeElement:first-child, .w50 .StripeElement:last-child {
	width: 50%;
	float: left;
	margin: 0;
}

.w50 .StripeElement:first-child {
	border-right: none;
	border-radius: 6px 0 0 6px;
}

.w50 .StripeElement:last-child {
	border-radius: 0 6px 6px 0;
}

.StripeElement.PaymentRequestButton {
    padding: 0;
}

.error {
    font-size: 13px;
    color: #FA755A;
    text-align: center;
    height: 40px;
    line-height: 40px;
    margin: 8px 0;
    width: 100%;
    font-weight: 500;
}

.error svg .base {
    fill: #fa755a;
}

.error svg .glyph {
    fill: #fff;
}

.error .message {
    color: #fff;
}


